import data from '../../data/index';
const { items, character } = data;

let arr = [];
let find_item;

character.equipment.forEach(e => {
    find_item = items.find(f => f.id === e[0]);
    if (e[1]) find_item.quantity = e[1];
    arr.push(find_item);
})

export default function equipment(state = arr, action) {
    switch(action.type) {
        case 'ADD_ITEM':
            const add = items.find(f => f.id === action.data);
            const check = state.find(f => f.id === action.data);
            if (check) {
                const x = [...state];
                x.map(m => {
                    if (m.id === add.id) {
                        m.quantity += add.quantity;
                        return m;
                    }
                    return m;
                })
                return state = x;
            } else {
                return state.concat(add);
            }
        case 'REMOVE_ITEM':
            const remove = state.find(f => f.id === action.data);
            if (remove.quantity === 1) {
                return state.filter(f => f.id !== remove.id);
            } else {
                const x = [...state];
                x.map(m => {
                    if (m === remove) {
                        m.quantity -= 1;
                        return m;
                    }
                    return m;
                })
                return state = x;
            }
        default:
            return state;
    }
}