import data from '../../data/index';

export default function quests(state = [ data.quests[0] ], action) {
    switch(action.type) {
        case 'ADD_QUEST':
            const add = data.quests.find(f => f.id === action.data)
            return state.concat(add);
        case 'UPDATE_QUEST':
            const update = state.find(f => f.id === action.data);
            if (update) {
                const x = [...state];
                x.map(m => {
                    if (m.id === update.id) {
                        m.current++;
                        return m;
                    }
                    return m;
                })
                return state = x;
            } 
            return state;
        case 'FINISH_QUEST':
            const finish = state.find(f => f.id === action.data);
            if (finish) {
                const x = [...state];
                x.map(m => {
                    if (m.id === finish.id) {
                        m.status = "finished";
                        m.current++;
                        return m;
                    }
                    return m;
                })
                return state = x;
            } 
            return state;
        case 'QUEST_FAILED':
            const failed = state.find(f => f.id === action.data);
            if (failed) {
                const x = [...state];
                x.map(m => {
                    if (m.id === failed.id) {
                        m.status = "failed";
                        return m;
                    }
                    return m;
                })
                return state = x;
            } 
            break;
        case 'REMOVE_QUEST':
            return state.filter(f => f.id !== action.data);
        default:
            return state;
    }
}