import data from '../../data/index';

export default function visited(state = data.NPC, action) {
    switch(action.type) {
        case 'PERSON_VISITED':
            const visit = state.find(f => f.id === action.data);
            if (visit) {
                const x = [...state];
                x.map(m => {
                    if (m.id === visit.id) {
                        m.visited = true;
                        return m;
                    }
                    return m;
                })
                return state = x;
            } 
            return state;
        default:
            return state;
    }
}